<template>
  <div>
    <div class="successHead">
      <div class="headLeft">
        <img src="static/cancel.png">
      </div>
      <div class="Headright">
        <span>取消成功！</span>
        <span>欢迎您下次再体验朱氏皮头针</span>
      </div>
    </div>
    <div class="block">
      <block-head :title="'预约信息'"></block-head>
      <div class="verbTime">
        <span>预约时间:</span>
        <span>{{this.$route.query.date}}</span>
        <hr>
      </div>
      <dotted-line></dotted-line>
      <info-row :left="'预约服务'" :right="'朱氏头皮针'"></info-row>
      <info-row :left="'服务价格'" :right="'200'"></info-row>
      <dotted-line></dotted-line>
      <info-row :left="'会员'" :right="this.$route.query.name" class="vipInfo"></info-row>
      <info-row :left="'性别'" :right="this.$route.query.sex" class="vipInfo"></info-row>
      <info-row :left="'年龄'" :right="this.$route.query.age" class="vipInfo"></info-row>
      <info-row :left="'手机号码'" :right="this.$route.query.telPhone" class="vipInfo"></info-row>
    </div>
    <div class="verbBox">
        <div class="verbBtn" @click="goVerb">
            <span>重新预约</span>
        </div>
    </div>
  </div>
</template>

<script>

import blockHead from 'components/blockHead/blockHead'
import dottedLine from 'components/dottedLine/dottedLine'
import infoRow from 'components/infoRow/infoRow'

export default {
  data () {
    return {
    }
  },
  components: {
    blockHead,
    dottedLine,
    infoRow
  },
  methods: {
    goVerb: function () {
      this.$router.push({
        path: `/doctorVerb`
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.successHead
    background-color white
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    .headLeft
        width 80px
        height 80px
        @extend .flex
        @extend .flex-around
        
    img
        width 50px
    .Headright
        @extend .flex
        @extend .flex-column
        justify-content space-around
        text-align left
        span
            line-height 20px
        span:first-of-type
            color black
        span:last-of-type
            color $color-theme-gray_1
            font-size $font-size-level_0

.block
    background-color white
    margin 10px 10px 0px 10px
    padding-bottom 20px
    .verbTime
        @extend .flex
        @extend .flex-row
        @extend .flex-start
        color $color-theme-gray_1
        position relative
        hr
            position absolute
            width 93%
            margin-left 15px
            top 22px
        span
            line-height 60px
            font-weight $font-weight-level_7
        span:first-of-type
            width 100px
        span:last-of-type
            flex-grow 2
            text-align right
            padding-right 10px
    .vipInfo
        margin-top 10px

.verbBox
    @extend .flex
    @extend .flex-row
    @extend .flex-around
    margin-top 20px
    padding-bottom 40px

.verbBtn
    width 70%
    height 40px
    background-color $color-theme-blue
    border-radius 3px
    span
        line-height 40px
        color white
        font-weight $font-weight-level_7
</style>


